
.contactInfo_item{
    font-size: 14px !important;
    color: #787878;
    line-height: 34px !important;
}

.contactInfo_item > span{
    color: black;
    font-weight: 600;
}

.contactInfo_socialsContainer{
    margin-top: 25px;
}

.contactInfo_social > a > .MuiSvgIcon-root {
    font-size: 21px !important;
    margin-right: 10px;
    color: black;
}

:root{
    --main-color: #ffc500;
  }
  
  .container_shadow {
    box-shadow: 0px 2px 92px 0px rgba(0,0,0,0.53);
    -webkit-box-shadow: 0px 2px 92px 0px rgba(0,0,0,0.20);
    -moz-box-shadow: 0px 2px 92px 0px rgba(0,0,0,0.33);
  }
  
  .top_60{
    margin-top: 60px !important;
    
  }
  
  .top_30 {
    margin-top: 30px !important;
  }
  .mb_-20{
    margin-bottom: -20px !important;
  }
  .mb_20 {
    margin-bottom: 20px !important;
  }
  
  .p_30{
    padding: 30px !important;
  }
  
  .pt_45{
    padding-top: 45px !important;
  }
  .pb_45 {
    padding-bottom: 45px !important;
  }
  
  .p_50{
    padding: 50px !important;
    margin-top: -40px !important;
  }
  @media (max-width: 991px) {
    .top_60{
      margin-top: 30px !important;
    }
  }
  
  .main-content {
    background-color: #F1F1F1;
    min-height: 100vh;
    border-radius: 6px;
    -moz-border-radius: 6px;
    -webkit-border-radius: 6px;
    margin-bottom: 30px;
  }
  
  .section_title{
    position: relative;
    display: inline-block;
    padding: 0 3px;
  }
  
  .section_title .section_title_text {
    font-size: 18px;
    font-weight: 600;
    position: relative;
    margin: 0;
    margin-bottom: 30px;
  }
  
  .section_title span {
    position: absolute;
    height: 7px;
    width: 100%;
    bottom: 0;
    opacity: 0.4;
    left: 0;
    
  }
  
  .graybg {
    background-color: #fafafa;
  }
  
  .section {
    padding-left: 30px;
    padding-right: 30px;
  }

  .box{
    margin: 0;
    position: absolute;
    top: 30%;
    left: 50%;
    transform: translate(-50%, -50%);
  }